.photo-card {
  height: auto;
  flex-basis: calc(25% - 15px);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-img {
  width: 350px;
  height: 220px;
  display: block;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.search-form input {
  border-color: #8a2be2;
  border-radius: 3px;
  margin-right: 10px;
  padding: 5px;
}

.search-form button {
  color: #f0f8ff;
  background-color: #8a2be2;
  border-radius: 3px;
  padding: 5px;
}

.info {
  text-align: center;
  display: flex;
}

.info-item > b {
  margin-bottom: 10px;
  font-size: 16px;
  display: inline-block;
}

.info-item > span {
  font-size: 14px;
}

.load-more {
  color: #f0f8ff;
  background-color: #8a2be2;
  border-radius: 3px;
  padding: 5px;
}

